<template>
    <div>
    <v-app id="inspire">
            <v-card>
              <v-toolbar dark color="primary">
                <v-toolbar-title>Reset Password</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                   <v-text-field v-model="username" id="username" prepend-icon="person" name="username" label="Username" type="text"></v-text-field>
                   <v-text-field v-model="password" id="password" prepend-icon="person" name="password" label="Password" type="password"></v-text-field>
                  <v-text-field v-model="newPassword" id="new" prepend-icon="lock" name="new" label="New Password" type="password"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="register">Change Password</v-btn>
              </v-card-actions>
            </v-card>
  </v-app>
    </div>
</template>

<script>
import Axios from 'axios';
export default {

    data() {
        return {
            password: '',
            newPassword: '',
            username: ''
        }
    },
    methods : {
        register () {
            const path = 'http://13.55.48.180:5000/password/reset'
            const payload = {
                newPassword : this.newPassword,
                Password: this.password,
                Username: this.username,
            }
            Axios.post(path,payload)
            this.$emit('sent')
        }
    }


}
</script>

