<script>
import axios from "axios";

export default {
  data() {
    return {
      hours: [],
      Id: "",
      Job: ""
    };
  },


  methods: {
    updateDate(date, Id, Job) {
      this.date = date;
      this.Id = Id;
      this.Job = Job;
      this.importData();
    },
    importData() {
      const path = "http://13.55.48.180:5000/hours/get/individual";
      const payload = {
        Date: this.date,
        employee_id: this.Id,
        job_id: this.Job
      };
      axios
        .post(path, payload)
        .then(res => {
          this.hours = res.data;
          console.log(res.data)
        })
        .catch(error => {
          axios.defaults.headers.common.Authorization = `Bearer ${localStorage.getItem(
            "token"
          )}`;
          this.Id = localStorage.getItem("Id");
          axios.post(path, payload).then(res => {
            this.hours = res.data;
          });
        });
    },
    exportData() {
      const path = "http://13.55.48.180:5000/hours/add";
      const date_send = {
        Date: this.date
      };
      const payload = {
        timeData: this.hours,
        date: this.date,
        ID: this.Id
      };
      axios
        .post(path, payload)
        .then(() => {
          this.importData();
          this.$store.dispatch("getAllJobs");
        })
        .catch(error => {
          this.importData();
        });
     this.$emit('saved');
    }
  },
  mounted() {
    axios.defaults.headers.common.Authorization = `Bearer ${this.$store.getters.getAuthKey.token}`;
    this.Id = this.$store.getters.getAuthKey.Id;
  }
};
